import Article, { Paragraph, Picture } from "../components/Article"

export default function AdministratorsDashboard() {
  return (
    <Article image="administrators/dashboard/cover.png">
      <Paragraph
        title="Un dashboard para dominarlo todo"
        paragraph="El dashboard es la pantalla principal de la aplicación, en él podrás acceder a cada rincón de la aplicación."
      />

      <Paragraph
        title="Conoce los widgets"
        paragraph="El dashboard para los usuarios de tipo administrador disponen de los siguientes widgets: Usuarios, que sirve la administrar los usuarios de la plataforma; Bosques urbanos, que sirve para administrar los bosques urbanos y sus áreas; Escuelas, que sirve para administrar las escuelas; y Tu perfil, que sirve para ver tu información y realizar ciertas cosas."
      />

      <Picture url={"administrators/dashboard/1.png"} />
    </Article>
  )
}
