import Main from "../components/Main"
import Container from "../components/Container"
import Flayer from "../components/Flayer"

export default function Administrators() {
  return (
    <Main image="administrators/cover.png">
      <Container title="Utilidades para los administradores">
        <Flayer
          title="Conoce tu dashboard"
          description="Aquí te decimos para qué sirve cada botón que aparece en el dashboard."
          screen="Dashboard del administrador"
          cover="administrators/dashboard/cover.png"
        />

        <Flayer
          title="Administración de usuarios"
          description="Crear, visualiza, edita y elimina usuarios de la plataforma."
          screen="Administración de usuarios"
          cover="administrators/users/cover.png"
        />

        <Flayer
          title="Administración de los bosques urbanos"
          description="Crear, visualiza, edita y elimina bosques urbanos de la plataforma."
          screen="Administración de los bosques urbanos"
          cover="administrators/places/cover.png"
        />

        <Flayer
          title="Administración de las áreas de los bosques urbanos"
          description="Crear, visualiza, edita y elimina las áreas de los bosques urbanos de la plataforma."
          screen="Administración de las áreas de los bosques urbanos"
          cover="administrators/areas/cover.png"
        />

        <Flayer
          title="Administración de escuelas"
          description="Crear, visualiza, edita y elimina las escuelas de la plataforma."
          screen="Administración de escuelas"
          cover="administrators/schools/cover.png"
        />
      </Container>
    </Main>
  )
}
