import Article, { Paragraph, Picture } from "../components/Article"

export default function Events() {
  return (
    <Article image="managers/events/cover.png">
      <Paragraph
        title="Administra los eventos de la plataforma"
        paragraph="Desde el widget de Eventos podrás administrar todos los eventos que estén en la plataforma, que va desde crear, visualizar, editar y eliminar."
      />

      <Picture url={"managers/events/1.png"} />

      <Paragraph
        title="Visualiza todos los eventos"
        paragraph="Nada más entrar a la pantalla se mostrarán los últimos eventos registrados de tu bosque y área que hay en la plataforma. Puedes buscar por nombre; filtrar por bosque urbano donde se realizará el evento y fecha de inicio; y también puedes optar por ver los eventos que ya han pasado."
      />

      <Picture url={"managers/events/2.png"} />

      <Paragraph
        title="Visualiza un evento"
        paragraph="Para visualizar un evento, basta con que presiones su tarjeta desde la lista de la pantalla donde se muestran todos los eventos. En esta pantalla se muestra toda la información y acciones que puedas realizar: publicar el evento si este está en cola, agregar prestadores o vincular un formulario."
      />

      <Picture url={"managers/events/3.png"} />

      <Paragraph
        title="Vincular un formulario"
        paragraph="Para poder realizar encuestas es necesario que primero vincules un formulario, para ello presiona el botón de vincular formulario."
      />

      <Picture url={"managers/events/4.png"} />

      <Paragraph paragraph="Al hacerlo se te abrirá la pantalla donde podrás buscar un formulario a través de su nombre, al seleccionar uno, este se vinculará en automático." />

      <Picture url={"managers/events/5.png"} />

      <Paragraph
        title="Inscribir prestadores"
        paragraph="Para poder agregar prestadores solo es cuestión de presionar el botón de Inscribir."
      />

      <Picture url={"managers/events/6.png"} />

      <Paragraph paragraph="Al hacerlo se te abrirá la pantalla donde podrás buscar a los prestadores a través de su nombre, al seleccionar uno, este se inscribirá en automático." />

      <Picture url={"managers/events/7.png"} />

      <Paragraph
        title="Edita un evento"
        paragraph="Dentro de los detalles de los eventos, en la parte inferior derecha se encuentra el botón para editar el evento, al presionarlo se abre la pantalla de edición, la cual muestra un formulario, donde los campos están rellenados con la información del evento. Simplemente es editar el campo a modificar y listo."
      />

      <Picture url={"managers/events/8.png"} />

      <Paragraph
        title="Elimina un evento"
        paragraph="Dentro de la pantalla de edición del evento, al final se encuentra el botón para eliminar el evento, simplemente es presionarlo y confirmar la acción."
      />

      <Picture url={"managers/events/9.png"} />

      <Paragraph
        title="Crea un evento nuevo"
        paragraph="En la pantalla donde se visualizan todos los eventos, en la parte inferior derecha se encuentra el botón para agregar evento, al presionarlo, nos lleva a otra pantalla donde se nos muestra un formulario donde puedes introducir los datos del evento a crear."
      />

      <Picture url={"managers/events/10.png"} />
    </Article>
  )
}
