import Article, { Paragraph, Picture } from "../components/Article"

export default function Profile() {
  return (
    <Article image="basics/profile/cover.png">
      <Paragraph
        title="Cómo acceder"
        paragraph="Como sabrás, nosotros tenemos cierta información personal sobre ti, por eso es importante que sepas cuales tenemos y si estas están correctas."
      />

      <Paragraph paragraph="Lo primero que tienes que hacer es dirigirte a la sección 'Tu perfil' dentro del dashboard, si tenemos una fotografía tuya aparecerá en lugar del icono." />

      <Picture url={"basics/profile/1.png"} />

      <Paragraph
        title="Sé como te llamas"
        paragraph="¡Listo! Ya puedes ver toda la información que tenemos sobre ti, la cual va desde tu nombre completo, donde estudias y a quién podemos comunicarnos en caso de una emergencia."
      />

      <Picture url={"basics/profile/2.png"} />

      <Paragraph
        title="¿Si es tu tipo de sangre?"
        paragraph="Algo que es importante mencionar es que revises detalladamente la información que tenemos sobre ti, ya que hay algunos campos que son necesarios e indispensables, asegúrate de mantener tu información correcta y actualizada."
      />
    </Article>
  )
}
