import { Flex, HStack, Spacer, VStack } from "@react-native-material/core"
import { useNavigation } from "@react-navigation/native"
import { Image } from "expo-image"
import { Button, Card, Text, useTheme } from "react-native-paper"

export default function Flayer({ title, description, screen, cover }) {
  const navigation = useNavigation()
  const theme = useTheme()

  return (
    <VStack
      fill
      w={300}
      minW={250}
      // maxW={450}
      m={10}
      // p={15}
      spacing={15}
      style={{ borderRadius: 10, borderWidth: 1, borderColor: theme.colors.outline, overflow: "hidden" }}
    >
      <Image
        source={cover !== undefined ? require(`../../assets/images/${cover}`) : require("../../assets/images/home.jpg")}
        contentFit="cover"
        style={{ width: "100%", height: 175 }}
      />
      <VStack
        spacing={15}
        ph={15}
      >
        <Text variant="titleLarge">{title}</Text>
        <Text variant="bodyLarge">{description}</Text>
      </VStack>
      <Spacer />
      <HStack
        justify="end"
        ph={15}
        pb={15}
      >
        <Button
          mode="contained"
          disabled={!screen}
          onPress={() => navigation.navigate(screen)}
        >
          Ver sección
        </Button>
      </HStack>
    </VStack>
  )
}
