import Article, { Paragraph, Picture } from "../components/Article"

export default function Users() {
  return (
    <Article image="administrators/users/cover.png">
      <Paragraph
        title="Administra los usuarios de la plataforma"
        paragraph="Desde el widget de Usuarios podrás administrar todos los usuarios que estén en la plataforma, que va desde crear, visualizar, editar y eliminar."
      />

      <Picture url={"administrators/users/1.png"} />

      <Paragraph
        title="Visualiza todos los usuarios"
        paragraph="Nada más entrar a la pantalla se mostrarán los últimos usuarios registrados de todas las áreas y roles que hay en la plataforma. Puedes buscar por nombre y registro; y filtrar por bosque urbano, área, rol, año de inscripción, periodo de inscripción, escuela, estado y tipo de prestador."
      />

      <Picture url={"administrators/users/2.png"} />

      <Paragraph
        title="Visualiza un usuario"
        paragraph="Para visualizar un usuario, basta con que presiones su tarjeta desde la lista de la pantalla donde se muestran todos los usuarios. En esta pantalla se muestra toda la información personal, así como su registro de horas en caso de que sea un prestador."
      />

      <Picture url={"administrators/users/3.png"} />

      <Paragraph
        title="Edita un usuario"
        paragraph="Dentro de los detalles de los usuarios, en la parte inferior derecha se encuentra el botón para editar el usuario, al presionarlo se abre la pantalla de edición, la cual muestra un formulario, donde los campos están rellenados con la información del usuario. Simplemente es editar el campo a modificar y listo."
      />

      <Picture url={"administrators/users/4.png"} />

      <Paragraph
        title="Elimina un usuario"
        paragraph="Dentro de la pantalla de edición del usuario, al final se encuentra el botón para eliminar el usuario, simplemente es presionarlo y confirmar la acción."
      />

      <Picture url={"administrators/users/5.png"} />

      <Paragraph
        title="Crea un usuario nuevo"
        paragraph="En la pantalla donde se visualizan todos los usuarios, en la parte inferior derecha se encuentra el botón para agregar usuario, al presionarlo, nos lleva a otra pantalla donde se nos muestra un formulario donde puedes introducir los datos del usuario a crear."
      />

      <Picture url={"administrators/users/6.png"} />
    </Article>
  )
}
