import "react-native-gesture-handler"
import { useSafeAreaInsets } from "react-native-safe-area-context"
import { StatusBar } from "expo-status-bar"
import { NavigationContainer } from "@react-navigation/native"
import { createNativeStackNavigator } from "@react-navigation/native-stack"
import { Appbar, PaperProvider, configureFonts } from "react-native-paper"
import * as SplashScreen from "expo-splash-screen"
import { useFonts } from "expo-font"
import { useColorScheme } from "react-native"
import { Dark, Light } from "./assets/Theme"

import Home from "./app/Home"
import Login from "./app/Basics/Login"
import Basics from "./app/Basics/Basics"
import ResetPassword from "./app/Basics/ResetPassword"
import Profile from "./app/Basics/Profile"
import Managers from "./app/managers/Managers"
import Administrators from "./app/administrators/Administrators"
import AdministratorsDashboard from "./app/administrators/AdministratorsDashboard"
import Users from "./app/administrators/Users"
import Places from "./app/administrators/Places"
import Areas from "./app/administrators/Areas"
import Schools from "./app/administrators/Schools"

import Providers from "./app/providers/Providers"
import ProvidersDashboard from "./app/providers/ProvidersDashboard"
import Progress from "./app/providers/Progress"
import SubscribeEvent from "./app/providers/SubscribeEvent"
import UnsubscribeEvent from "./app/providers/UnsubscribeEvent"
import QR from "./app/providers/qr"
import Proximity from "./app/providers/proximity"
import UpdatePassword from "./app/basics/UpdatePassword"
import ManagersDashboard from "./app/managers/ManagersDashboard"
import Events from "./app/managers/Events"
import Logout from "./app/Basics/Logout"
import Hours from "./app/managers/Hours"
import OngoingEvents from "./app/managers/OngoingEvents"
import Forms from "./app/managers/Forms"
import Surveys from "./app/managers/Surveys"

SplashScreen.preventAutoHideAsync()
const Stack = createNativeStackNavigator()

export default function App() {
  const scheme = useColorScheme()
  const [fontsLoaded] = useFonts({
    LexendDeca: require("./assets/fonts/LexendDeca.ttf")
  })

  if (!fontsLoaded) {
    return null
  } else {
    const fontConfig = {
      fontFamily: "LexendDeca"
    }

    const theme =
      scheme === "dark"
        ? {
            ...Dark,
            fonts: configureFonts({ config: fontConfig })
          }
        : {
            ...Light,
            fonts: configureFonts({ config: fontConfig })
          }

    console.log("Esquema", scheme)

    return (
      <>
        <StatusBar style="auto" />
        <PaperProvider theme={theme}>
          <NavigationContainer theme={theme}>
            <Stack.Navigator screenOptions={{ header: (props) => <AppBar {...props} /> }}>
              <Stack.Screen
                name="Inicio"
                component={Home}
              />

              <Stack.Group navigationKey="Basics">
                <Stack.Screen
                  name="Aspectos básicos"
                  component={Basics}
                />

                <Stack.Screen
                  name="Inicio de sesión"
                  component={Login}
                />

                <Stack.Screen
                  name="Restablece tu contraseña"
                  component={ResetPassword}
                />

                <Stack.Screen
                  name="Consulta tu información"
                  component={Profile}
                />

                <Stack.Screen
                  name="Actualiza tu contraseña"
                  component={UpdatePassword}
                />
                
                <Stack.Screen
                  name="Cierra tu sesión"
                  component={Logout}
                />
              </Stack.Group>

              <Stack.Group navigationKey="Providers">
                <Stack.Screen
                  name="Cuentas de prestadores"
                  component={Providers}
                />

                <Stack.Screen
                  name="Dashboard del prestador"
                  component={ProvidersDashboard}
                />

                <Stack.Screen
                  name="Revisa tu progreso"
                  component={Progress}
                />

                <Stack.Screen
                  name="Inscríbete a un evento"
                  component={SubscribeEvent}
                />
                
                <Stack.Screen
                  name="Cancela tu inscripción a un evento"
                  component={UnsubscribeEvent}
                />

                <Stack.Screen
                  name="Registra tu asistencia mediante QR"
                  component={QR}
                />

                <Stack.Screen
                  name="Registra tu asistencia mediante proximidad"
                  component={Proximity}
                />
              </Stack.Group>

              <Stack.Group navigationKey="Managers">
                <Stack.Screen
                  name="Cuentas de encargados"
                  component={Managers}
                />

                <Stack.Screen
                  name="Dashboard del encargado"
                  component={ManagersDashboard}
                />

                <Stack.Screen
                  name="Administración de eventos"
                  component={Events}
                />
              </Stack.Group>

              <Stack.Group navigationKey="Administrators">
                <Stack.Screen
                  name="Cuentas de administradores"
                  component={Administrators}
                />

                <Stack.Screen
                  name="Dashboard del administrador"
                  component={AdministratorsDashboard}
                />

                <Stack.Screen
                  name="Administración de usuarios"
                  component={Users}
                />

                <Stack.Screen
                  name="Administración de horas"
                  component={Hours}
                />

                <Stack.Screen
                  name="Administración de eventos en curso"
                  component={OngoingEvents}
                />
                
                <Stack.Screen
                  name="Administración de encuestas"
                  component={Surveys}
                />
                
                <Stack.Screen
                  name="Administración de formularios"
                  component={Forms}
                />

                <Stack.Screen
                  name="Administración de los bosques urbanos"
                  component={Places}
                />

                <Stack.Screen
                  name="Administración de las áreas de los bosques urbanos"
                  component={Areas}
                />

                <Stack.Screen
                  name="Administración de escuelas"
                  component={Schools}
                />
              </Stack.Group>
            </Stack.Navigator>
          </NavigationContainer>
        </PaperProvider>
      </>
    )
  }
}

const AppBar = (props) => {
  const { top } = useSafeAreaInsets()
  return (
    <Appbar.Header
      safeAreaInsets={{ top }}
      mode="center-aligned"
      elevated={true}
    >
      {props.navigation.canGoBack() && <Appbar.BackAction onPress={() => props.navigation.goBack()} />}
      <Appbar.Content title={props.route.name} />
    </Appbar.Header>
  )
}
