import Article, { Paragraph, Picture } from "../components/Article"

export default function Areas() {
  return (
    <Article image="administrators/areas/cover.png">
      <Paragraph
        title="Administra las áreas de los bosques urbanos de la plataforma"
        paragraph="Las área de los bosques urbanos se encuentran dentro del detalle del bosque urbano en cuestión, no se encuentra en un widget separado."
      />

      <Picture url={"administrators/areas/1.png"} />

      <Paragraph
        title="Visualiza todas las áreas del bosque urbano"
        paragraph="Nada más entrar a la pantalla de los detalles del bosque urbano se mostrarán las últimas áreas registradas."
      />

      <Picture url={"administrators/areas/2.png"} />

      <Paragraph
        title="Edita un área del bosque urbano"
        paragraph="Dentro de los detalles del bosque urbano, en la sección de áreas se listan las áreas que hay, al presionar una tarjeta se abre la pantalla de edición, la cual muestra un formulario, donde los campos están rellenados con la información del área. Simplemente es editar el campo a modificar y listo."
      />

      <Picture url={"administrators/areas/3.png"} />

      <Paragraph
        title="Elimina un área del bosque urbano"
        paragraph="Dentro de la pantalla de edición del área, al final se encuentra el botón para eliminar el área, simplemente es presionarlo y confirmar la acción."
      />

      <Picture url={"administrators/areas/4.png"} />

      <Paragraph
        title="Crea un área nueva"
        paragraph="En la pantalla donde se visualizan los detalles del bosque urbano, en la sección de áreas se encuentra el botón para agregar un área, al presionarlo, nos lleva a otra pantalla donde se nos muestra un formulario donde puedes introducir los datos del área a crear."
      />

      <Picture url={"administrators/areas/5.png"} />
    </Article>
  )
}
