import Article, { Paragraph, Picture } from "../components/Article"

export default function ManagersDashboard() {
  return (
    <Article image="managers/dashboard/cover.png">
      <Paragraph
        title="Un dashboard para dominarlo todo"
        paragraph="El dashboard es la pantalla principal de la aplicación, en él podrás acceder a cada rincón de la aplicación."
      />

      <Paragraph
        title="Conoce los widgets"
        paragraph="El dashboard para los usuarios de tipo encargado disponen de los siguientes widgets: A continuación, te ayuda a conocer si un evento está proximo, está por comenzar o está en curso; Eventos, desde aquí podrás revisar los eventos próximos que habrán; Usuarios, esta sección está pensada para que puedas administrar los prestadores de los que dispones; Formulario, desde aquí podrás administrar los formularios para las encuestas; y Tu perfil, que sirve para ver tu información y realizar ciertas cosas."
      />

      <Picture url="managers/dashboard/1.png" />
    </Article>
  )
}
