import Article, { Paragraph, Picture } from "../components/Article"

export default function Places() {
  return (
    <Article image="administrators/places/cover.png">
      <Paragraph
        title="Administra los bosques urbanos de la plataforma"
        paragraph="Desde el widget de Bosques urbanos podrás administrar todos los bosques urbanos que estén en la plataforma, que va desde crear, visualizar, editar y eliminar."
      />

      <Picture url={"administrators/places/1.png"} />

      <Paragraph
        title="Visualiza todos los bosques urbanos"
        paragraph="Nada más entrar a la pantalla se mostrarán los últimos bosques urbanos registrados que hay en la plataforma. Puedes buscar por nombre."
      />

      <Picture url={"administrators/places/2.png"} />

      <Paragraph
        title="Visualiza un bosque urbano"
        paragraph="Para visualizar un bosque urbano, basta con que presiones su tarjeta desde la lista de la pantalla donde se muestran todos los bosques urbanos. En esta pantalla se muestra toda la información."
      />

      <Picture url={"administrators/places/3.png"} />

      <Paragraph
        title="Edita un bosque urbano"
        paragraph="Dentro de los detalles de los bosques urbanos, en la parte inferior derecha se encuentra el botón para editar el bosque urbano, al presionarlo se abre la pantalla de edición, la cual muestra un formulario, donde los campos están rellenados con la información del bosque urbano. Simplemente es editar el campo a modificar y listo."
      />

      <Picture url={"administrators/places/4.png"} />

      <Paragraph
        title="Elimina un bosque urbano"
        paragraph="Dentro de la pantalla de edición del bosque urbano, al final se encuentra el botón para eliminar el bosque urbano, simplemente es presionarlo y confirmar la acción."
      />

      <Picture url={"administrators/places/5.png"} />

      <Paragraph
        title="Crea un bosque urbano nuevo"
        paragraph="En la pantalla donde se visualizan todos los bosques urbanos, en la parte inferior derecha se encuentra el botón para agregar bosque urbano, al presionarlo, nos lleva a otra pantalla donde se nos muestra un formulario donde puedes introducir los datos del bosque urbano a crear."
      />

      <Picture url={"administrators/places/6.png"} />
    </Article>
  )
}
