import Article, { Paragraph, Picture } from "../components/Article"

export default function Schools() {
  return (
    <Article image="administrators/schools/cover.png">
      <Paragraph
        title="Administra las escuelas de la plataforma"
        paragraph="Desde el widget de Escuelas podrás administrar todas las escuelas que estén en la plataforma, que va desde crear, visualizar, editar y eliminar."
      />

      <Picture url="administrators/schools/1.png" />

      <Paragraph
        title="Visualiza todas las escuelas"
        paragraph="Nada más entrar a la pantalla se mostrarán las últimas escuelas registradas. Puedes buscar por nombre."
      />

      <Picture url="administrators/schools/2.png" />

      <Paragraph
        title="Visualiza una escuela"
        paragraph="Para visualizar una escuela, basta con que presiones su tarjeta desde la lista de la pantalla donde se muestran todas las escuelas. En esta pantalla se muestra toda la información de la misma."
      />

      <Picture url="administrators/schools/3.png" />

      <Paragraph
        title="Edita una escuela"
        paragraph="Dentro de los detalles de las escuelas, en la parte inferior derecha se encuentra el botón para editar la escuela, al presionarlo se abre la pantalla de edición, la cual muestra un formulario, donde los campos están rellenados con la información de la escuela. Simplemente es editar el campo a modificar y listo."
      />

      <Picture url="administrators/schools/4.png" />

      <Paragraph
        title="Elimina una escuela"
        paragraph="Dentro de la pantalla de edición de la escuela, al final se encuentra el botón para eliminar la escuela, simplemente es presionarlo y confirmar la acción."
      />

      <Picture url="administrators/schools/5.png" />

      <Paragraph
        title="Crea una escuela nueva"
        paragraph="En la pantalla donde se visualizan todas las escuelas, en la parte inferior derecha se encuentra el botón para agregar una escuela, al presionarlo, nos lleva a otra pantalla donde se nos muestra un formulario donde puedes introducir los datos de la escuela a crear."
      />

      <Picture url="administrators/schools/6.png" />
    </Article>
  )
}
